export function generateUrl(path) {
  let rootUrl = "/";

  // for github pages
  if (process.env.NODE_ENV == "production") {
    // console.log('in production');
    rootUrl = "/trello-clone-with-react-context-api/"
  } else {
    console.log('not in production');
  }

  return rootUrl + path;

}


